<template>
<div class="activity">
    <HeaderBar></HeaderBar>
    <div class="topbar">
        <h4 class="pagetitle">
            活動管理
        </h4>
        <div class="searchwrap">
            <SearchBar></SearchBar>
        </div>

        <div class="addactivity">
            <i class="ri-calendar-event-fill"></i>
            <span>新增活動</span>
        </div>
    </div>
    <div class="content">
        <div class="lefttab">
            <div v-for="(item,index) in itemlist" :key="index">
                <Tabitem :tabname="item.name" :isall=isnotall :isclick="item.isclick" @emittabitem=clicktabitem(index)></Tabitem>
            </div>

        </div>
        <div class="activitylist">
        </div>
    </div>
</div>
</template>

<script>
// @ is an alias to /src
import HeaderBar from '../components/Header'
import SearchBar from '../components/Searchbar'
import Tabitem from '../components/Tabitem'
export default {
    name: 'Home',
    data() {
        return {
            itemlist: [{
                name: '所有',
                isclick: true
            }, {
                name: '場館一',
                isclick: false
            }, {
                name: '場館二',
                isclick: false
            }, {
                name: '場館三',
                isclick: false
            }],
            isall: true,
            isnotall: false
        }
    },
    methods:{
      clicktabitem(val){
        for(let i = 0 ; i<this.itemlist.length;i++){
          if(this.itemlist[i].isclick==true){
            this.itemlist[i].isclick = false
            // console.log(this.itemlist[i],i)
          }
        }
        this.itemlist[val].isclick = true
      }
    },
    components: {
        HeaderBar,
        SearchBar,
        Tabitem
    }
}
</script>

